import { Directive, Renderer2, HostListener, OnInit, ElementRef, HostBinding } from '@angular/core';
import { ClickType } from '@swimlane/ngx-datatable';
import { Input } from '@angular/core';

@Directive({
  selector: '[appVideo]'
})
export class VideoDirective implements OnInit{
  @Input() name: string;
  constructor(private render:Renderer2,private el:ElementRef) { }
 
  @HostBinding('style.backgroundColor') backgroundColor:string="transparent";
  @HostBinding('style.color') clr:string="white";
  ngOnInit()
  {
    console.log(this.name);
  }
  @HostListener('click') ClickType()
    {
      console.log("clcick");
        this.backgroundColor="#FE6700";
        this.clr="white";
    }
    
   

}
